<template>
  <div class="container">
    <div class="header">
      <el-radio-group v-model="queryInfo.type" @change="handleClick">
        <el-radio-button label="-1">全部</el-radio-button>
        <el-radio-button label="1">男</el-radio-button>
        <el-radio-button label="2">女</el-radio-button>
        <el-radio-button label="3">组合</el-radio-button>
      </el-radio-group>
      <el-radio-group v-model="queryInfo.area" @change="handleClick">
        <el-radio-button label="-1">全部</el-radio-button>
        <el-radio-button label="7">华语</el-radio-button>
        <el-radio-button label="96">欧美</el-radio-button>
        <el-radio-button label="8">日本</el-radio-button>
        <el-radio-button label="16">韩国</el-radio-button>
        <el-radio-button label="0">其他</el-radio-button>
      </el-radio-group>
    </div>
    <ArtistsList :artists="artists"></ArtistsList>
    <div class="footer">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :page-size="queryInfo.limit"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import ArtistsList from "@/components/ArtistsList.vue";
export default {
  components: {
    ArtistsList,
  },
  data() {
    return {
      queryInfo: {
        type: "-1",
        area: "-1",
        limit: 20,
        offset: 0,
      },
      artists: [],
      total: 100,
      currentPage: 1,
    };
  },
  methods: {
    async getArtists() {
      const { data: res } = await this.$http.get("/artist/list", {
        params: this.queryInfo,
      });
      // console.log(res);
      this.artists = res.artists;
    },
    handleClick() {
      this.currentPage = 1;
      this.queryInfo.offset = 0;
      this.getArtists();
    },
    handleCurrentChange() {
      this.queryInfo.offset = (this.currentPage - 1) * this.queryInfo.limit;
      this.getArtists();
    },
  },
  created() {
    this.getArtists();
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
  .header {
    .el-radio-group {
      display: block;
      margin: 20px;
    }
  }
  .footer {
    display: flex;
    justify-content: center;
  }
}
</style>